'use strict';

import mongoose from 'mongoose';
import {registerEvents} from './testimonial.events';
import {Schema} from 'mongoose';
var TestimonialSchema = new mongoose.Schema({
  name:{
  	type:String
  },
  image:{
  	type:String
  },
  rating:{
    type:Number
  },
  message:{
  	type:String
  },
  companyName:{
  	type:String
  },
  createdAt:{
  	type:Date,
  	default:Date.now()
  }
});

registerEvents(TestimonialSchema);
export default mongoose.model('Testimonial', TestimonialSchema);
